"use client";
import React, { useEffect, useState } from "react";
import { useRouter } from "next/navigation";
import { OrdersTable } from "./components/orders-table";
import { getColumns } from "./columns";
import { AddOrderDialog } from "./components/add-order-dialog";
import { Button } from "@/components/ui/button";
import { StatusDropdown } from "./components/StatusDropdown";
import { useOrders } from "./hooks/useOrders";

export default function OrdersPage() {
  const router = useRouter();
  const [addDialogOpen, setAddDialogOpen] = useState(false);

  const {
    orders,
    loading,
    page,
    pageSize,
    total,
    setPage,
    fetchOrders,
    searchTerm,
    setSearchTerm,
    customerFilter,
    setCustomerFilter,
    statusFilter,
    setStatusFilter,
    sortField,
    setSortField,
    sortDir,
    setSortDir,
    clearFilters,
  } = useOrders();

  // âœ… CLIENT GUARD: redirect if token missing
  useEffect(() => {
    const token = localStorage.getItem("token");
    if (!token) router.replace("/login");
  }, [router]);

  const logout = () => {
    localStorage.removeItem("token");
    router.replace("/login");
  };

  return (
    <div className="p-4 space-y-4">
      {/* Header */}
      <div className="flex items-center justify-between">
        <h1 className="text-2xl font-bold">Orders</h1>

        <div className="flex items-center gap-2">
          <Button variant="outline" onClick={logout}>Logout</Button>
          <Button onClick={() => setAddDialogOpen(true)}>Add Order</Button>
        </div>
      </div>

      {/* Filters */}
      <div className="flex flex-wrap gap-2 items-center">
        <input
          className="border rounded px-2 py-1 text-sm"
          placeholder="Global search..."
          value={searchTerm}
          onChange={(e) => setSearchTerm(e.target.value)}
        />
        <input
          className="border rounded px-2 py-1 text-sm"
          placeholder="Customer filter..."
          value={customerFilter}
          onChange={(e) => setCustomerFilter(e.target.value)}
        />
        <StatusDropdown
          options={["OPEN", "PENDING", "INPROGRESS", "COMPLETED", "CANCELLED"]}
          selected={statusFilter}
          onChange={setStatusFilter}
        />
        <Button size="sm" variant="outline" onClick={clearFilters}>Clear Filters</Button>
      </div>

      {/* Add Order Dialog */}
      <AddOrderDialog
        open={addDialogOpen}
        onClose={() => setAddDialogOpen(false)}
        refreshData={() => fetchOrders(1, pageSize)}
      />

      {/* Orders Table */}
      <OrdersTable
        data={orders}
        page={page}
        pageSize={pageSize}
        total={total}
        loading={loading}
        onPageChange={setPage}
        refreshData={fetchOrders}
        columns={getColumns(fetchOrders)}
        sortField={sortField}
        sortDir={sortDir}
        setSortField={setSortField}
        setSortDir={setSortDir}
      />
    </div>
  );
}
